@use '../base/var' as *;
@use '../base/breakpoints' as *;

/* ===== PRICING ===== */
.pricing {
  padding-bottom: 60px;
  @include media-breakpoint-up(md) {
    padding-bottom: 80px;
  }
  @include media-breakpoint-up(xl) {
    padding-bottom: 60px;
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    @include media-breakpoint-up(md) {
      flex-direction: row;
      flex-wrap: wrap;
    }

    li {
      position: relative;
      display: flex;
      flex-direction: column;
      @include media-breakpoint-up(md) {
        flex-basis: calc(50% - 10px);
        &:nth-child(1) {padding-top: 42px;order: 1;}
        &:nth-child(2) {order: 3;}
        &:nth-child(3) {padding-top: 42px;order: 2;}
      }
      @include media-breakpoint-up(lg) {
        padding-top: 40px !important;
        flex-basis: calc(33.3% - 14px);
        &:nth-child(1) {order: 2;}
        &:nth-child(2) {order: 3;}
        &:nth-child(3) {order: 1;}
      }
      @include media-breakpoint-up(xl) {
        flex-basis: calc(33.3% - 13px);
      }
    }
  }

  &__entry {
    padding: 24px;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    z-index: 2;
    color: $color-charcoal-gray;
    background: $color-white;
    box-shadow: inset 0 0 0 1px $color-mist;
    @include media-breakpoint-up(md) {
      font-size: 16px;
      line-height: 24px;
      flex: 1;
    }

    p {
      margin-bottom: 20px;
    }
  }

  &__accent {
    .pricing__entry {
      box-shadow: inset 0 0 0 2px rgba($color-blue, .3);
      @include media-breakpoint-up(xl) {
        box-shadow: inset 0 0 0 2px $color-neutral;
      }
    }
  }

  &__pre {
    padding: 8px 8px 32px;
    border-radius: 29px 29px 0 0;
    margin: 0 0 -24px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    position: relative;
    z-index: 1;
    font-size: 16px;
    line-height: 1.5;
    font-weight: normal;
    text-align: center;
    color: $color-blue;
    background: $color-neutral;
    cursor: default;
    @include user-select;
    @include media-breakpoint-up(md) {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
    }

    svg, span {
      display: block;
    }
  }

  &__title {
    margin-bottom: 18px !important;
    font-size: 24px;
    line-height: 32px;
    color: $color-dark;
    @include media-breakpoint-up(md) {
      margin-bottom: 16px !important;
      font-size: 28px;
      line-height: 36px;
    }

    small {
      font-size: 18px;
      color: $color-charcoal-gray;
      @include media-breakpoint-down(xss) {
        font-size: 16px;
      }
    }
  }

  &__discount {
    padding: 4px 12px;
    border-radius: 32px;
    display: block;
    position: absolute;
    top: 26px;
    right: 24px;
    font-size: 14px;
    line-height: 20px;
    color: $color-green;
    background: rgba($color-green, .1);
    @include media-breakpoint-up(md) {
      top: 28px;
    }
  }

  .btn {
    margin-top: auto;
  }
}